:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
/* .app{
  display:grid;
} */
.appcontainer {
  background-color: var(--light);
  width: 100%;
  height: 100%;
  min-height: 600px;
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-rows: 1fr;
}
.left {
  position: sticky;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  padding: 10px;

  height: 100%;
  grid-row: 1/4;
  background-color: var(--dark);
  color: var(--light);
}
.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  /* margin-top: 10px; */
}

.img {
  width: 30px;
  height: 30px;
  color: var(--green);
}
.logo h3 {
  font-size: 1.3rem;
  font-weight: normal;
}
.links ul li {
  list-style-type: none;
  padding: 10px;
  opacity: 0.8;
  font-size: 1.2rem;
}
.links ul li:hover {
  cursor: pointer;
  opacity: 1;
  color: var(--indigo);
}
header {
  width: 100%;
  height: 50px;
  padding: 10px 20px;
  background-color: var(--white);
  box-shadow: 0 0 20px rgb(115 105 215 / 15%);
}
.hedercontainer {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu h3 {
  opacity: 0.7;
}
.menu h3:hover {
  color: var(--indigo);
  cursor: pointer;
}
.icons svg {
  height: 25px;
  width: 25px;
  margin: 10px;
  opacity: 0.6;
}
.icons svg:hover {
  cursor: pointer;
  opacity: 1;
  color: var(--indigo);
}
.maincontainer {
  display: flex;
  flex-direction: column;
}
.watchlist {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 10px;
  flex-wrap: wrap;
}
.watchlistitms {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.watchlistitms div {
  width: 120px;
  /* height: 70px; */
  background-color: var(--white);
  border-radius: 5px;
  box-shadow: 0 0 20px rgb(115 105 215 / 15%);
  text-align: center;
  padding: 10px;
}
.watchlistitms div:hover {
  background-color: var(--gray-dark);
  color: white;
  cursor: pointer;
}

.columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin: auto;
  flex-wrap: wrap;
}
.maincontainer h3 {
  color: var(--indigo);
}
.portfolio,
.news,
.overview,
.performance {
  padding: 20px;
  text-transform: capitalize;
  text-align: center;
}
.portfolio div,
.news .newsitm,
.overview div,
.performance div {
  background-color: #fff;
  box-shadow: 0 0 20px rgb(115 105 215 / 15%);
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 10px;
  height: 100%;
}

ul li {
  list-style-type: none;
}

.portfolio div li,
.performance div li {
  padding: 10px;
}
.head {
  opacity: 0.7;
  color: var(--purple);
}
.stocks {
  opacity: 0.5;
}

/* .news{
text-align: center;
}*/
.news .newsitm {
  display: flex;
  flex-direction: column;
  /* justify-content: flex-start; */
  align-items: flex-start;
  text-align: left;
}
.newsitm h4 {
  opacity: 0.8;
  color: var(--purple);
}
.newsitm p {
  opacity: 0.5;
  font-size: 0.8rem;
}
.newsitm div {
  box-shadow: 0 0 20px rgba(18, 17, 32, 0.15);
  padding: 20px;
  width: 100%;
}
.newsitm div:hover {
  background-color: var(--gray-dark);
  color: var(--white);
  cursor: pointer;
}

.overview div,
.performance div {
  color: var(--light);
  background-color: var(--gray-dark);
  box-shadow: 0 0 20px var(--cyan);
}
.overview img {
  width: 100%;
  height: 300px;
}
.green {
  color: var(--green);
}
.red {
  color: var(--danger);
}
footer {
  margin-top: 50px;
  padding: 20px;
  padding-bottom: 10px;
  background-color: var(--white);
  box-shadow: 0 0 20px rgba(18, 17, 32, 0.15);
  /* color: var(--light); */
  font-size: 1.1rem;
  text-align: center;
}
footer span {
  /* font-size: 1rem; */
  opacity: 0.5;
}
.lines,
.left p {
  display: none;
}
.lines:hover,
.left p:hover {
  cursor: pointer;
}
@media screen and (max-width: 1000px) {
  .left {
    position: fixed;
    transform: translateX(-300px);
    top: 0;
    height: 100%;
    width: 30%;
    opacity: 0.95;
    z-index: 999;
  }
  .left p {
    text-align: right;
    align-self: flex-end;
    display: block;
    color: red;
  }

  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }
  .lines {
    display: block;
  }
  .line {
    height: 2px;
    width: 20px;
    background-color: var(--gray-dark);
    margin: 5px;
  }
  .appcontainer {
    grid-template-columns: 1fr;
  }
}
@media screen and (min-width: 1000px) {
  .left {
    transform: translateX(0) !important;
  }
}
@media screen and (max-width: 768px) {
  .columns {
    grid-template-columns: 1fr;
  }
}

